<template>
  <div class="container">
    <div class="col-sm-10">
      <h1>Задачи (выполнено: {{completed_count}}. Не выполнено: {{un_completed_count}}.)</h1>
      <confirmation
        :message="confirmationMessage"
        :showDismissibleAlert="showConfirmation"
        v-if="showConfirmation"
      ></confirmation>
      <button
        type="button"
        id="task-add"
        class="btn btn-success btn-sm align-left d-block"
        v-b-modal.todo-modal
      >Добавить задачу</button>

      <table class="table table-dark table-stripped table-hover">
        <thead class="thead-light">
        <tr>
          <th>Uid</th>
          <th>Описание</th>
          <th>Выполнена?</th>
          <th></th>
        </tr>
        </thead>

        <tbody>
        <tr v-for="(todo, index) in todos" :key="index">
          <td class="todo-uid">{{ todo.uid }}</td>
          <td>{{ todo.description }}</td>
          <td>
            <span v-if="todo.is_completed">Выполнено</span>
            <span v-else>Не выполнено</span>
          </td>
          <td>
            <div class="btn-group" role="group">
              <button
                type="button"
                class="btn btn-secondary btn-sm"
                v-b-modal.todo-update-modal
                @click="updateTodo(todo)"
              >Обновить</button>
              &nbsp;
              <button
                type="button"
                class="btn btn-danger btn-sm"
                @click="deleteTodo(todo)"
              >X</button>
            </div>
          </td>
        </tr>
        </tbody>
      </table>
      <b-modal ref="addTodoModal" id="todo-modal" title="Добавить задачу" hide-footer>
        <b-form @submit="onSubmit" @reset="onReset" class="w-100">
          <b-form-group
            id="form-description-group"
            label="Описание:"
            label-for="form-description-input"
          >
            <b-form-input
              id="form-description-input"
              type="text"
              v-model="addTodoForm.description"
              required
              placeholder="Завести задачу"
            ></b-form-input>
          </b-form-group>
          <b-form-group id="form-complete-group">
            <b-form-checkbox-group v-model="addTodoForm.is_completed" id="form-checks">
              <b-form-checkbox value="true">Задача выполнена?</b-form-checkbox>
            </b-form-checkbox-group>
          </b-form-group>
          <b-button type="submit" variant="primary">Добавить</b-button>
          <b-button type="reset" variant="danger">Сброс</b-button>
        </b-form>
      </b-modal>

      <b-modal ref="updateTodoModal" id="todo-update-modal" title="Update" hide-footer>
        <b-form @submit="onUpdateSubmit" @reset="onUpdateReset" class="w-100">
          <b-form-group
            id="form-update-description-group"
            label="Описание:"
            label-for="form-update-description-input"
          >
            <b-form-input
              id="form-update-description-input"
              type="text"
              v-model="updateTodoForm.description"
              required
            ></b-form-input>
          </b-form-group>
          <b-form-group id="form-update-complete-group">
            <b-form-checkbox-group v-model="updateTodoForm.is_completed" id="form-update-checks">
              <b-form-checkbox value="true">Задача выполнена?</b-form-checkbox>
            </b-form-checkbox-group>
          </b-form-group>
          <b-button-group>
            <b-button type="submit" variant="primary">Обновить</b-button>
            <b-button type="reset" variant="danger">Сброс</b-button>
          </b-button-group>
        </b-form>
      </b-modal>
    </div>
  </div>
</template>

<style>
  button#task-add {
    margin-top: 20px;
    margin-bottom: 20px;
  }
  h1,
  td {
    text-align: left;
  }
  .todo-uid {
    text-align: right;
  }
</style>

<script>
import Confirmation from './Confirmation.vue';

export default {
  name: 'Todo',
  data() {
    return {
      todos: [],
      addTodoForm: {
        description: '',
        is_completed: [],
      },
      updateTodoForm: {
        uid: 0,
        description: '',
        is_completed: [],
      },
      confirmationMessage: '',
      showConfirmation: false,
      completed_count: 0,
      un_completed_count: 0,
    };
  },
  methods: {
    getTodos() {
      const tasksJS = localStorage.getItem('tasks');
      if (tasksJS) {
        this.todos = JSON.parse(tasksJS);
        this.completed_count = this.todos.filter(item => item.is_completed === true).length;
        this.un_completed_count = this.todos.length - this.completed_count;
      }
    },
    resetForm() {
      this.addTodoForm.description = '';
      this.addTodoForm.is_completed = [];
      this.updateTodoForm.description = '';
      this.updateTodoForm.is_completed = [];
    },
    onSubmit(event) {
      event.preventDefault();
      this.$refs.addTodoModal.hide();
      const tasksJS = localStorage.getItem('tasks');
      let tasks = [];
      if (tasksJS) {
        tasks = JSON.parse(tasksJS);
        tasks.push({
          uid: tasks.length + 1,
          description: this.addTodoForm.description,
          is_completed: this.addTodoForm.is_completed[0] ? true : false,
        });
        this.confirmationMessage = `Задача "${this.addTodoForm.description}" добавлена`;
        this.showConfirmation = true;
      } else {
        tasks = [
          {
            uid: 1,
            description: this.addTodoForm.description,
            is_completed: this.addTodoForm.is_completed[0] ? true : false,
          },
        ];
      }
      localStorage.setItem('tasks', JSON.stringify(tasks));
      this.getTodos();
      this.resetForm();
    },
    onReset(event) {
      event.preventDefault();
      this.$refs.addTodoModal.hide();
      this.resetForm();
    },
    updateTodo(todo) {
      this.updateTodoForm = todo;
    },
    onUpdateSubmit(event) {
      const tasksJS = localStorage.getItem('tasks');
      let tasks = [];
      if (tasksJS) {
        tasks = JSON.parse(tasksJS);
        const task = tasks.find(item => item.uid === this.updateTodoForm.uid);
        if (task) {
          task.is_completed = this.updateTodoForm.is_completed ? true : false;
          task.description = this.updateTodoForm.description;
          if (task.is_completed) {
            this.completed_count++;
          }
          this.confirmationMessage = 'Задача обновлена';
          this.showConfirmation = true;
          localStorage.setItem('tasks', JSON.stringify(tasks));
        }
      }
    },
    onUpdateReset(event) {
      event.preventDefault();
      this.$refs.updateTodoModal.hide();
      this.resetForm();
    },
    deleteTodo(todo) {
      const tasksJS = localStorage.getItem('tasks');
      let tasks = [];
      if (tasksJS) {
        tasks = JSON.parse(tasksJS);
        const taskIndex = tasks.findIndex(item => item.uid === todo.uid);
        if (taskIndex >= 0) {
          tasks.splice(taskIndex, 1);
          this.getTodos();
          this.confirmationMessage = 'Задача удалена из списка';
          this.showConfirmation = true;
        }
      }
      localStorage.setItem('tasks', JSON.stringify(tasks));
      this.getTodos();
      this.resetForm();
    },
  },
  components: {
    confirmation: Confirmation,
  },
  created() {
    this.getTodos();
  },
};
</script>
